<template>
        <div class="lightingBtn" @click="bindTap">
                <slot></slot>
        </div>
</template>
<script setup>
import { nuxtTo } from '@/utils/tools'

const props = defineProps({
        url: {
                type: String,
                default: ''
        },
        id: {
                type: Number,
                default: 0
        }
})
const { url, id } = toRefs(props)
const bindTap = () => {
        const query = id.value && { idx: id.value }
        nuxtTo(url.value, query)
}
</script>
